<template>
  <v-app id="vbl">
    <o-dash v-if="dashboard == 'org'"></o-dash>
    <public-dash v-if="dashboard == 'public'"></public-dash>
    <v-content>
      <v-container fluid>
        <router-view></router-view>
        <v-layout row v-if="buttons">
          <v-flex>
            <v-btn @click="dashboard = 'public'">Public</v-btn>
            <v-btn @click="dashboard = 'org'">Organization</v-btn>
            <v-btn @click="dashboard = 'player'">Player</v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  import OrgDash from './components/Nav/Dashboards/Organization.vue'
  import PublicDash from './components/Nav/Public.vue'
  export default {
    data: () => ({
      dashboard: 'public',
      buttons: false
    }),
    components: {
      'o-dash': OrgDash,
      'public-dash': PublicDash
    }
  }
</script>


