import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import Profile from '@/components/User/Profile'
import Join from '@/components/Account/Register'
import SignIn from '@/components/Account/Login'
import Players from '@/components/Pages/Public/Players'
import Rankings from '@/components/Pages/Public/Rankings'
import Tournaments from '@/components/Pages/Public/Tournaments'
import Organizations from '@/components/Pages/Public/Organizations'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/join',
      name: 'Join',
      component: Join
    },
    {
      path: '/signin',
      name: 'SignIn',
      component: SignIn
    },
    {
      path: '/players',
      name: 'Players',
      component: Players
    },
    {
      path: '/rankings',
      name: 'Rankings',
      component: Rankings
    },
    {
      path: '/tournaments',
      name: 'Tournaments',
      component: Tournaments
    },
    {
      path: '/organizations',
      name: 'Organizations',
      component: Organizations
    }
  ],
  mode: 'history'
})



