<template>
  <v-container>
    <v-layout row wrap>
      <v-flex>
        <h1>{{fullname}}</h1>
        <v-card>
          <v-card-text>
            hello
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
  
</template>

<script>
export default {
  data () {
    return {
      firstName: 'Jonathan',
      middleName: '',
      lastName: 'Alvarez',
      gender: '',
      birthDate: ''
    }
  },
  computed: {
    fullname () {
      let middle = this.middleName ? ' ' + this.middleName + ' ' : ' '
      return this.firstName + middle + this.lastName
    }
  }
}
</script>

<style>

</style>



