export default {
  state: {
    loading: false,
    error: null,
    loginDialog: false
  },
  mutations: {
    setLoading (state, payload) {
      state.loading = payload
    },
    setError (state, payload) {
      state.error = payload
    },
    clearError (state) {
      state.error = null
    },
    setLoginDialog (state, payload) {
      state.loginDialog = payload
    }
  },
  actions: {
    clearError ({commit}) {
      commit('clearError')
    },
    showLoginDialog ({commit}) {
      commit('setLoginDialog', true)
    },
    closeLoginDialog ({commit}) {
      commit('setLoginDialog', false)
    }
  },
  getters: {
    loading (state) {
      return state.loading
    },
    error (state) {
      return state.error
    },
    userIsAuthenticated (state) {
      return true
      // return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    }
  }
}



