<template>
    <v-layout row wrap class="text-xs-center">
        <v-flex>
            <h1>{{ title }}</h1>
        </v-flex>
    </v-layout>
</template>

<script>
export default {
  props: [
    'title'
  ]
}
</script>

<style scoped>
    h1:after {
        background-color: #85cddb;
        height: 3px;
        content: '';
        position: relative;
        display: block;
        margin: 15px auto;
    }
</style>




